import React, { useLayoutEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
//import IconButton from "@material-ui/core/IconButton";
//import MenuIcon from "@material-ui/icons/Menu";
import Button from "@material-ui/core/Button";

function useWindowSize() {
  const [size, setSize] = useState([0, 0]);

  useLayoutEffect(() => {
    function updateSize() {
      if (window.innerWidth < 343) {
        setSize([0, 0]);
      } else if (window.innerWidth < 480) {
        setSize([window.innerWidth, window.innerHeight * 0.35]);
      } else if (window.innerWidth < 720) {
        setSize([window.innerWidth, window.innerHeight * 0.5]);
      } else if (window.innerWidth < 768) {
        setSize([window.innerWidth, window.innerHeight * 0.55]);
      } else if (window.innerWidth < 900) {
        setSize([window.innerWidth, window.innerHeight * 0.6]);
      } else if (window.innerWidth < 1024) {
        setSize([window.innerWidth, window.innerHeight * 0.7]);
      } else {
        setSize([window.innerWidth, window.innerHeight * 0.91]);
      }
    }
    window.addEventListener("resize", updateSize);
    updateSize();
    return () => window.removeEventListener("resize", updateSize);
  }, []);
  return size;
}

export default function DenseAppBar(props) {
  const classes = useStyles();
  const [width, height] = useWindowSize();
  return (
    <React.Fragment>
      <AppBar position="fixed" className={classes.appbar}>
        <Toolbar variant="dense">
          <Typography variant="h6">
            <img
              widht={width / 18}
              height={height / 18}
              src={require("../../../assets/images/logo.png")}
              style={{ marginTop: 5 }}
            ></img>
          </Typography>
          <div className={classes.buttons}>
            <Button onClick={props.clickParaiba} className={classes.button}>
              Paraíba
            </Button>
            <Button onClick={props.clickBrazil} className={classes.button}>
              Brasil
            </Button>
            <Button onClick={props.clickWorld} className={classes.button}>
              Mundo
            </Button>
          </div>
        </Toolbar>
      </AppBar>
    </React.Fragment>
  );
}

const useStyles = makeStyles((theme) => ({
  appbar: {
    backgroundColor: "white",
    borderBottom: "2px solid black",
    //"#00b885"
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  buttons: {
    marginLeft: "auto",
  },
  button: {
    color: "#010126",
    border: "none",
    "&:hover": {
      border: "1px solid white",
      backgroundColor: "#ff5e5e",
      color: "white",
    },
    borderRadius: 8,
    marginLeft: 6,
  },
}));
