import React, { useState, useLayoutEffect } from "react";
//import { useStyles } from "./styles";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";

function useWindowSize() {
  const [size, setSize] = useState([0, 0]);

  useLayoutEffect(() => {
    function updateSize() {
      if (window.innerWidth < 290) {
        setSize([0, 0]);
      } else if (window.innerWidth < 320) {
        setSize([window.innerWidth, window.innerHeight * 0.15]);
      } else if (window.innerWidth < 480) {
        setSize([window.innerWidth, window.innerHeight * 0.3]);
      } else if (window.innerWidth < 720) {
        setSize([window.innerWidth, window.innerHeight * 0.5]);
      } else if (window.innerWidth < 768) {
        setSize([window.innerWidth, window.innerHeight * 0.55]);
      } else if (window.innerWidth < 900) {
        setSize([window.innerWidth, window.innerHeight * 0.5]);
      } else if (window.innerWidth < 1024) {
        setSize([window.innerWidth, window.innerHeight * 0.6]);
      } else {
        setSize([window.innerWidth, window.innerHeight * 0.91]);
      }
    }
    window.addEventListener("resize", updateSize);
    updateSize();
    return () => window.removeEventListener("resize", updateSize);
  }, []);
  return size;
}

const imgs = {
  img1: {
    url: require("./../../../../assets/images/foto1.png"),
  },
  img2: {
    url: require("./../../../../assets/images/foto2.png"),
  },
};

export const Image = (props) => {
  const [width, height] = useWindowSize();
  return <img src={props.src} width={width} height={height} />;
};

export const Begin = () => {
  return (
    <Carousel autoPlay>
      <a href="https://covid.saude.gov.br/" target="_blank">
        <div>
          <Image src={imgs.img1.url} />
        </div>
      </a>
      <a href="https://covid.saude.gov.br/" target="_blank">
        <div>
          <Image src={imgs.img2.url} />
        </div>
      </a>
    </Carousel>
  );
};

